import React from 'react';
import './Categories.css';
import CatalogItem from "components/CatalogItem/CatalogItem";


const Categories = () => {

    const catalog = [
        {catalogNameEn: 'laptop', catalogNameRu: ''},
        {catalogNameEn: 'desktop', catalogNameRu: ''},
        {catalogNameEn: 'monitor', catalogNameRu: ''},
        {catalogNameEn: 'cpu', catalogNameRu: ''},
        {catalogNameEn: 'motherboard', catalogNameRu: ' '},
        {catalogNameEn: 'videocard', catalogNameRu: ''},
        {catalogNameEn: 'ssd', catalogNameRu: ' '},
        {catalogNameEn: 'ram', catalogNameRu: ' '},
        {catalogNameEn: 'cpu-cooling', catalogNameRu: '  '},
        {catalogNameEn: 'psu', catalogNameRu: ' '},
        {catalogNameEn: 'case', catalogNameRu: ''},
        {catalogNameEn: 'fans', catalogNameRu: '  '}
    ];


    return (
        <section className={'Categories'}>
            <h2 className='categories-header montserrat'> </h2>
          {catalog.map((item) =>
            <CatalogItem catalogNameEn={item.catalogNameEn} catalogNameRu={item.catalogNameRu}/>
          )}
        </section>
    );
};

export default Categories;